import 'output.css';
import React, {useState} from "react";

import {BrowserRouter} from "react-router-dom";
import {GradesScreen} from "./Prof/Grades/GradesScreen";
import {ProfSidebar} from "./Common/component/sidebar/ProfSidebar";
import {Appbar} from "./Common/component/appbar/Appbar";
import {Box} from "@mui/material";


function App() {
    let showAppbar = 'block lg:hidden'
    const [isVisible, setIsVisible] = useState(false);

    const toggleVisibility = () => {
        setIsVisible((prev) => !prev); // Toggles between true and false
    };

    return (
        <BrowserRouter>
            <div className={`w-full h-dvh flex flex-row relative bg-background dark`}>
                <div className={`h-fit z-40`}>
                    <div className={showAppbar}>
                        <Appbar className='!z-0 w-full' onClick={toggleVisibility}/>
                    </div>
                    <ProfSidebar onDismiss={toggleVisibility} className={`z-40 h-full`}
                                 isVisible={isVisible}/>
                </div>
                <div className='flex flex-col gap-14 w-full h-full'>
                    <div className={`${showAppbar} bg-background`}/>
                    <GradesScreen className={'overflow-scroll'}/>
                </div>
            </div>
        </BrowserRouter>
    );
}

export default App;



